import styled from "styled-components";
import SlideBox from "../component/SlideBox";
import Img1 from "../images/third/1.png";
import Img2 from "../images/third/2.png";
import Img3 from "../images/third/3.png";
import { motion } from "framer-motion";

const ThirdList = () =>{
  return(
    <Component initial={{opacity:0,translateX:200}}
    animate={{opacity:1,translateX:0}}
    transition={{duration:1}}
    exit={{opacity:0}}>
      <Title>
        <h1> </h1>  
        <p>2022.12 - 2023.01</p>   
      </Title>
      <Main>
        <section>
          <SlideBox img={[Img1,Img2,Img3]}></SlideBox>
        </section>
        <section>
          <Skill>
            <div>
              <div style={{width:100}}><b> </b></div>
              <div style={{textAlign:'left',width:'70%'}}>,  , CCTV    ,  ,  </div>
            </div>
            <div>
              <div style={{width:100}}><b> GitHub</b></div>
              <div style={{textAlign:'left',width:'70%'}}><a style={{textDecoration: "none",color:'tomato',fontWeight:'bold'}} href="https://github.com/Jeong-Yoon-Hwan/ProjectB_.git">https://github.com/Jeong-Yoon-Hwan/ProjectB_.git</a></div>
            </div>
            <div>
              <div style={{width:100}}><b> Front-end</b></div>
              <div style={{textAlign:'left',width:'70%'}}>react, typescript</div>
            </div>
            <div>
              <div style={{width:100}}><b> Back-end</b></div>
              <div style={{textAlign:'left',width:'70%'}}>node.js, express</div>
            </div>
            <div>
              <div style={{width:100}}><b> Database</b></div>
              <div style={{textAlign:'left',width:'70%'}}>mysql</div>
            </div>
            
          </Skill>
          <Content style={{fontSize:13}}>
            <div>
              <h3>  </h3>
               api       .
            </div><br/>
            <h3>  </h3><br/>
            <div>
              <h3>  </h3><br/>
              <p>   API ,    ,      . </p>
              <p>        .</p>
              <p>  API    ,      .</p>
            </div><br/>
            <div>
              <h3> CCTV </h3><br/>
              <p>  CCTV   ,    REST API       .</p>
            </div><br/>
            <div>
              <h3> ,  </h3><br/>
              <p> ,              .</p>
            </div><br/>
            
          </Content>
        </section>
      </Main>
    </Component>
  )
}
export default ThirdList;

const Component = styled(motion.div)`
  width:100%;
  height:100%;
  background-color: white;
  display: flex;
  flex-direction:column;
  gap:20px;
  border-radius: 10px;
  
`

//
const Title = styled.div`
  width:100%;
  height:100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap:20px;
  padding:10px;

  & > h1 {
    font-family: var(--font-DoHyeon);
  }
  & > p {
    color:gray;
  }
`

// ,   ,  
const Main = styled.div`
  width:100%;
  height:max-content;
  display: flex;
  flex-direction: column;

  //  
  & > section {
    width:100%;
    height: max-content;
    //background-color: lightgray;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  // 
  & > section:nth-child(2){
    padding:20px;
    background-color: white;

    display: flex;
    flex-direction: row;
    @media screen and (max-width: 1100px) {
        flex-direction: column;
        width:100%;
        gap:20px;
    }
  }
`

// 
const Skill = styled.div`
  width:40%;
  height:100%;
  display: flex;
  flex-direction: column;
  color:#333;
  font-size: 15px;
  gap:20px;
  border-right: 2px solid gray;
  padding-right: 10px;
  padding-bottom: 20px;

  & > div {
    display: flex;
    justify-content: center;
    align-items: start;
    & > a {
      text-decoration-line: none;
      
    }
  }

  @media screen and (max-width: 1100px) {
    width:100%;
    border-bottom: 2px solid gray;
    border-right: none;
  }

`

// 
const Content = styled.div`
  color:#333;
  font-weight: bolder;
  width:60%;
  height:100%;
  padding-left:20px;
  display: flex;
  flex-direction: column;
  gap:5px;

  @media screen and (max-width: 1100px) {
  width:100%;
}


`